import React from "react";
import PropTypes from "prop-types";

export const Warning = (props) => {
    let style = {
        zIndex: 2,
        left: 0,
        top: 0,
        backgroundColor: "rgba(0,0,0,0.5)"
    }
    let contentStyle = {
        margin: "15% auto",
        width: "80%",
        top: "15%",
        position: "sticky"
    }
    let numberOfButtons = 0;
    if (props.showAccept) numberOfButtons++;
    if (props.showCancel) numberOfButtons++;
    const colSize = numberOfButtons > 1 ? '3' : '4';
    return (
        <div className="u-w-100 u-h-100 u-position-absolute" style={style}>
            <div className="u-padding-all-l g-white1-bg" style={contentStyle}>
                <a href="#" className="o-modal__close" onClick={props.onCancel} style={{textDecoration: "none"}}>
                    <span className="u-acc-hide">Zamknij okno</span>
                </a>
                <div className="l-row">
                    <div className="l-col l-col-12 ">
                        <h2 className="u-margin-l u-margin-top-l">{props.modalTitle}</h2>
                        <div className="u-margin-l-xl" dangerouslySetInnerHTML={{__html: props.modalContents}}/>
                    </div>
                </div>
                <div className="l-row">
                    {
                        props.showAccept &&
                        <div className={`l-col l-col-small-12 l-col-medium-${colSize} l-col-${colSize} u-right u-text-right u-padding-top-l-xl`}>
                            <button className="o-btn opl-btn opl-btn--primary opl-btn-medium u-w-100" onClick={props.onAccept}>
                                <span className="opl-ripple-box">
                                    <span className="opl-ripple-circle"/>
                                </span>
                                {props.onAcceptLabel}
                            </button>
                        </div>
                    }
                    {
                        props.showCancel &&
                        <div className={`l-col l-col-small-12 l-col-medium-${colSize} l-col-${colSize} u-right u-text-right u-padding-top-l-xl`}>
                            <button className="o-btn opl-btn opl-btn--secondary opl-btn-medium u-w-100" onClick={props.onCancel}>
                                <span className="opl-ripple-box">
                                    <span className="opl-ripple-circle"/>
                                </span>
                                {props.onCancelLabel}
                            </button>
                        </div>
                    }
                </div>
            </div>
        </div>
    );
}

Warning.propTypes = {
    modalTitle: PropTypes.string,
    modalContents: PropTypes.string,
    onAccept: PropTypes.func,
    onCancel: PropTypes.func,
    onAcceptLabel: PropTypes.string,
    onCancelLabel: PropTypes.string,
    showAccept: PropTypes.bool,
    showCancel: PropTypes.bool
}

Warning.defaultProps = {
    modalTitle: "Uwaga",
    onAcceptLabel: "Wybieram",
    onCancelLabel: "Anuluj",
    showAccept: true,
    showCancel: true
}